import './App.css';
import { Mafs, Plot, Coordinates, useMovablePoint, Theme, Transform, Text } from "mafs"

function App() {
  const a = useMovablePoint([-3.1,1.2], {
    color: Theme.blue
  })
  
  const b = useMovablePoint([3.2,-1.2], {
    color: Theme.red
  })


  return (
    <Mafs>
      <Plot.VectorField
        xy={([x, y]) => [
          -(x-a.x)/Math.pow(Math.pow(x-a.x,2)+Math.pow(y-a.y,2),1.5)+(x-b.x)/Math.pow(Math.pow(x-b.x,2)+Math.pow(y-b.y,2),1.5),
          -(y-a.y)/Math.pow(Math.pow(x-a.x,2)+Math.pow(y-a.y,2),1.5)+(y-b.y)/Math.pow(Math.pow(x-b.x,2)+Math.pow(y-b.y,2),1.5),
        ]}
        step={0.5}
        xyOpacity={([x, y]) => Math.sqrt(
          Math.pow(-(x-a.x)/Math.pow(Math.pow(x-a.x,2)+Math.pow(y-a.y,2),1.5)+(x-b.x)/Math.pow(Math.pow(x-b.x,2)+Math.pow(y-b.y,2),1.5),2)+
          Math.pow(-(y-a.y)/Math.pow(Math.pow(x-a.x,2)+Math.pow(y-a.y,2),1.5)+(y-b.y)/Math.pow(Math.pow(x-b.x,2)+Math.pow(y-b.y,2),1.5),2)
  )}
      />
      {a.element}
      {b.element}
    </Mafs>
  )
}

export default App;
